import { createNamespacedHelpers } from 'vuex';
const { mapState, mapMutations } = createNamespacedHelpers('storeCommodity');
var myreg = /^(((13[0-9]{1})|(14[0-9]{1})|(17[0-9]{1})|(15[0-3]{1})|(15[5-9]{1})|(18[0-9]{1})|(19[0-9]{1}))+\d{8})$/;
var myregs = /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,16}$/;
export default {
    data() {
        return {
			cd:false,
			phone:"",
			password:"",
			isPassword:"",
			isBtn:false,
			btnTxt:"获取验证码",
			verificationCode:"",
			...mapState(['isLogin'])
		};
	},
	methods:{
		...mapMutations(['LOGIN']),
		// 检测手机是否被注册
		checkPhone(){
			if(myreg.test(this.phone)){
				this.$request.POST({
					url:this.$api.apiUrl.POST_USER_CHECK,
					data:{
						username:this.phone
					}
				}).then(res=>{
					if(res.errcode === -1){
						this.$api.msg(res.errmsg)
						this.phone = '';
					}
				})
			}else{
				this.$api.msg("请输入正确的手机号码")
			}
		},
		// 两次密码必须相同
		passwordIdentical(){
			if(!myregs.test(this.password)){this.$api.msg("密码需在6-16位数字和字母的组合"); return false;}
			if(this.password!==this.isPassword){
				this.$api.msg("你输入的密码不相同,请核实")
				return false;
			}
			return true
		},
		getCode(){
			// 判断手机号是否正确
			if(!this.phone){this.$api.msg("输入正确的手机号");return false};
			// 判断两次密码是否相同
			let isRight = this.passwordIdentical();
			if(isRight){
				this.$request.POST({
					url:this.$api.apiUrl.POST_SMS_GETCODE,
					data:{
						phone:this.phone
					}
				}).then(res=>{
					this.codeDisabled();
				})
			};
		},
		//设置请求验证码倒计时
		codeDisabled(){
			// 按钮禁用
			this.isBtn = true;
			// 倒计时事件
			let time = 60;
			// 设置请求验证码倒计时
			let timeInterval = setInterval(()=>{
				if(time>0){
					this.btnTxt = (time--) + "秒后"
				}else{
					this.isBtn=false;
					this.btnTxt = "重新发送"
					time = 60;
					clearInterval(timeInterval);
				}
			},1000)
		},
		// 跳转到协议页面
		toAgreement(){
			uni.navigateTo({
				url:"/pages/indexs/useragreement/useragreement"
			})
		},
		isChecked(){this.cd = !this.cd;},
		submit(){
			if(!this.cd){this.$api.msg("请勾选已阅读,多多美平台协议");return false;}
			this.$request.POST({
				url:this.$api.apiUrl.POST_SMS_CHECK,
				data:{
					code:this.verificationCode,
					phone:this.phone
				}
			}).then(res=>{
				if(res.errCode===200){
					this.$request.POST({
						url:this.$api.apiUrl.POST_USER_REGISTER,
						data:{
							username:this.phone,
							password:this.password,
							confirm_password:this.isPassword,
						}
					}).then(res=>{
						this.userLogin();
						if(res.errcode === 200){
							uni.showToast({
								title:"注册成功",
								success:()=>{this.userLogin();}
							})
						}else{
							this.$api.msg(res.errmsg)
						}
					})
				}else{
					this.$api.msg(res.errMsg)
				}
			})
		},
		userLogin(){
			this.$request.POST({
				url:this.$api.apiUrl.POST_USER_LOGIN,
				data:{
					username:this.phone,
					password:this.password
				}
			}).then(res => {
				if(res.code === 200){
					uni.setStorage({
						key: 'access_token',
						data: res.data,
						success:()=>{
							Promise.all([this.LOGIN({"isLogin":!this.isLogin()})]).then(() =>{
								uni.showToast({
									title: res.msg
								});
								uni.switchTab({
									url:"/pages/mys/user/user"
								});
							});		
						},
						fail:()=>{
							uni.showToast({
								title: "Token设置失败，请自行登录"
							});
						}
					});
				}else{
					uni.showToast({
						title:res.msg,
						icon:"none"
					});
				}
			})
		}
	}
};
